<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="content" v-html="content.ContentInfo"></div>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar,
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('mycenter.Help'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      content:''
    }
  },
  created(){
    this.help()
  },
  methods:{
    help(){
      var that = this
      var id = this.$route.query.id
      this.axios({
        url:this.Url + 'SystemContent.ashx',
        method:'post',
        params: {req:5,id:id}
      }).then(res=>{
        if (res.data.success) {
          this.headerNavData.title = res.data.data.Title
          this.content = res.data.data
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.content{
  padding: .3rem
}
</style>



